.userReplyContainer {
	border-bottom: 1px solid #e6ecf0;
	padding: 16px 23px;
	flex: 1;
}

.userReplyList {
	display: flex;
	gap: 8px;
}

.userReplyInfo {
	display: flex;
	flex-direction: column;
	flex: 1;
	gap: 8px;
}

.userReplySecInfo {
	width: 100%;
	display: flex;
	justify-content: space-between;
}

.userReplyNameGroup {
	display: flex;
	flex-direction: row;
	gap: 7px;
}

.userReplySmallAccount {
	display: flex;
	flex-direction: row;
	color: #6c757d;
	align-items: center;
	font-weight: 400;
	font-size: 14px;
	gap: 1px;
}

.userReplyTime {
	display: flex;
	gap: 2px;
}

.userReplyTime {
	display: flex;
}

.userReplyName {
	font-weight: 700;
	font-size: 16px;
}

.userReplyContent {
	display: flex;
	gap: 8px;
}

.userReplyAvatar {
	position: relative;
	width: 50px;
	height: 50px;
	border-radius: 50%;
	overflow: hidden;
}

.userReplyImg {
	display: block;
	position: relative;
	height: 100%;
	left: 50%;
	transform: translateX(-50%);
}

.replyListItemAccountGroup {
	display: flex;
	gap: 3px;

	.replyListItemText {
		color: #6c757d;
	}
	.replyListItemAccount {
		color: #ff6600;
		line-height: 22px;
		cursor: pointer;
	}
}

.userReplyQuantityGroup {
	display: flex;
	gap: 40px;
	color: #6c757d;

	.userReplyQuantity {
		display: flex;
		gap: 8px;
		align-items: center;
	}
	.userReplyLikeQuantity {
		display: flex;
		gap: 8px;
		align-items: center;
	}
}
