.popularUserContainer {
	display: flex;
	align-items: center;
	justify-content: center;
	height: 82px;
	width: 100%;
}

.popularUserList {
	display: flex;
	gap: 10px;
}

.popularUserItems {
	display: flex;
	align-items: center;
	justify-content: space-between;
	gap: 15px;
}

.popularUserName {
	color: #171725;
	font-weight: 700;
	font-size: 16px;
	white-space: nowrap;
	text-overflow: ellipsis;
	overflow: hidden;
	width: 83px;
}

.popularUserAccount {
	color: #92929d;
	font-weight: 500;
	font-size: 14px;
	line-height: 22px;
}

.popularUserLogo {
	width: 50px;
	height: 50px;
	border-radius: 50%;
	position: relative;
	overflow: hidden;
}

.popularUserImg {
	height: 100%;
	display: block;
	position: relative;
	left: 50%;
	transform: translateX(-50%);
}

.popularUserButton {
	height: 40px;
	width: 96px;
	display: flex;
	justify-content: end;
}

.popularUserFollower {
	width: 64px;
	height: 40px;
}
