*,
*::before,
*::after {
	box-sizing: border-box;
}

html,
body {
	width: 100%;
	height: 100%;
	overflow-y: hidden;
	overflow-x: hidden;
}

body {
	font-family: 'Noto Sans TC', sans-serif;
	font-size: 1rem;
	line-height: 1.4;
	margin: 0;
}

h1,
h2,
h3,
h4,
h5 {
	margin: 0;
	line-height: 0.9;
}

p {
	margin: 0;
}

ul,
li {
	list-style-type: none;
	margin: 0;
	padding: 0;
}

a {
	text-decoration: none;
	color: black;
}
