.replyListItemList {
	display: flex;
	gap: 8px;
	padding: 16px 24px;
}

.replyListItemInfo {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	gap: 8px;
}

.replyListItemImg {
	width: 50px;
	height: 50px;
	border-radius: 50%;
}

.replyListItemNameGroup {
	display: flex;
	gap: 8px;

	.replyListItemSmallAccount {
		display: flex;
		color: #6c757d;
	}
	.replyListItemTimeGroup {
		display: flex;
		gap: 2px;
	}
}

.replyListItemAccountGroup {
	display: flex;
	gap: 3px;

	.replyListItemText {
		color: #6c757d;
	}
	.replyListItemAccount {
		color: #ff6600;
		cursor: pointer;
	}
}
